/* 텍스트 숨기기 */
.hidden {
  text-indent: -9999px;
}

.main-wrap {
  width: 100%;
  height: 100vh;
  margin: 0 auto;
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: center;
}

/* 메인 타이틀 */
.main-title {
  font-size: 146px;
  padding: 0;
  margin: 0;
  padding-bottom: 50px;
}

/* 핸드 아이콘, 애니메이션  */
.main-hand-icon {
  width: 246px;
  height: 246px;
  background: url(../assets/hand_icon.svg) no-repeat 0 0 / auto 246px;
  margin-bottom: 80px;
  -webkit-animation: hand 2s infinite steps(3);
  animation: hand 2s infinite steps(3);
}

@-webkit-keyframes hand {
  100% {
    background-position: -738px 0;
  }
}

@keyframes hand {
  100% {
    background-position: -738px 0;
  }
}

/* 게임 시작 버튼 */
.start-btn {
  display: block;
  border: none;
  padding: 16px 36px;
  font-size: 42px;
  border-radius: 50px;
  color: #fff;
  background: #6F00FF;
  cursor: pointer;
  -webkit-transition: all .4s;
  transition: all .4s;
}

.start-btn:hover {
  color: #FFF000;
}
